'use client';

import { useState } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import Image from 'next/image';

const clones = [
  {
    name: "Sophie",
    age: 28,
    occupation: "Tech Entrepreneur",
    interests: ["AI", "Yoga", "Travel"],
    image: "/images/clones/sophie.jpg",
  },
  {
    name: "James",
    age: 32,
    occupation: "Data Scientist",
    interests: ["Machine Learning", "Rock Climbing", "Photography"],
    image: "/images/clones/james.jpg",
  },
  {
    name: "Emma",
    age: 26,
    occupation: "UX Designer",
    interests: ["Art", "Hiking", "Coffee"],
    image: "/images/clones/emma.jpg",
  },
];

export const CloneExamples = () => {
  const [activeIndex, setActiveIndex] = useState(0);
  const [hoveredIndex, setHoveredIndex] = useState<number | null>(null);

  const nextClone = () => {
    setActiveIndex((prev) => (prev + 1) % clones.length);
  };

  const prevClone = () => {
    setActiveIndex((prev) => (prev - 1 + clones.length) % clones.length);
  };

  return (
    <section className="py-20 ocean-gradient">
      <div className="container mx-auto px-4">
        <h2 className="text-4xl font-bold text-center mb-16 text-white">
          Meet Our Example Clones
        </h2>
        
        <div className="relative max-w-4xl mx-auto">
          <button
            onClick={prevClone}
            className="absolute left-0 top-1/2 -translate-y-1/2 z-10 bg-white/20 p-2 rounded-full"
          >
            ‚Üê
          </button>
          
          <button
            onClick={nextClone}
            className="absolute right-0 top-1/2 -translate-y-1/2 z-10 bg-white/20 p-2 rounded-full"
          >
            ‚Üí
          </button>
          
          <div className="overflow-hidden">
            <AnimatePresence mode="wait">
              <motion.div
                key={activeIndex}
                initial={{ opacity: 0, x: 100 }}
                animate={{ opacity: 1, x: 0 }}
                exit={{ opacity: 0, x: -100 }}
                transition={{ duration: 0.5 }}
                className="flex items-center gap-8"
              >
                <div className="relative w-96 h-96">
                  <Image
                    src={clones[activeIndex].image}
                    alt={clones[activeIndex].name}
                    fill
                    className="object-cover rounded-lg"
                  />
                </div>
                
                <div className="flex-1 text-white">
                  <h3 className="text-3xl font-bold mb-4">
                    {clones[activeIndex].name}, {clones[activeIndex].age}
                  </h3>
                  <p className="text-xl mb-4">{clones[activeIndex].occupation}</p>
                  <div className="flex flex-wrap gap-2">
                    {clones[activeIndex].interests.map((interest) => (
                      <span
                        key={interest}
                        className="px-3 py-1 bg-white/20 rounded-full text-sm"
                      >
                        {interest}
                      </span>
                    ))}
                  </div>
                </div>
              </motion.div>
            </AnimatePresence>
          </div>
          
          <div className="flex justify-center mt-8 gap-2">
            {clones.map((_, index) => (
              <button
                key={index}
                onClick={() => setActiveIndex(index)}
                className={`w-3 h-3 rounded-full ${
                  index === activeIndex ? "bg-white" : "bg-white/30"
                }`}
              />
            ))}
          </div>
        </div>
      </div>
    </section>
  );
};
